@media screen and (max-width: 1075px) {
  .menu {
    -webkit-flex-direction: row;
            flex-direction: row;
    min-height: var(--header-height);
    padding: 0px 40px;
    -webkit-align-items: center;
            align-items: center;
    box-shadow: 0px 4px 16px rgb(0 0 0 / 10%);
  }

  .menu__maincontent {
    height: auto;
    position: fixed;
    top: -100%;
    left: 0;
    right: 0;
    width: 92%;
    margin: 0 auto;
    text-align: center;
    background-color: var(--contrast-color);
    transition: 0.4s;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    border-radius: 30px;
    padding: 20px;
    box-sizing: border-box;
    z-index: 100;
  }

  .menu__social {
    -webkit-flex-direction: row;
            flex-direction: row;
    max-width: inherit;
    margin: 0 auto;
    padding: 35px 20px;
    box-sizing: border-box;
    grid-gap: 50px;
    gap: 50px;
    width: auto;
  }

  .menu__social a {
    font-size: 40px;
  }

  .menu__delimiter {
    display: none;
  }

  .menu__logo {
    display: block;
    font-size: 45px;
  }

  .menu__toggle {
    display: block;
  }

  .menu__toggle.active line:nth-child(2) {
    opacity: 0;
  }

  .menu__toggle.active line:first-child {
    -webkit-transform: translate(2px, 0px) rotate(45deg);
            transform: translate(2px, 0px) rotate(45deg);
    transition: 0.5s;
  }

  .menu__toggle.active line:last-child {
    -webkit-transform: translate(-3px, 3px) rotate(-45deg);
            transform: translate(-3px, 3px) rotate(-45deg);
    transition: 0.5s;
  }

  .menu__navbar a {
    font-size: 25px;
    margin-bottom: 40px;
  }
}

