    font-size: 75px;
  }
}

.portfolio-card {
  max-width: 400px;
  min-height: 300px;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  border-radius: 10px;
  box-shadow: 5px 5px 7px 4px #d9d7d7;
  transition: 0.5s;
}

.portfolio-card:hover {
  box-shadow: 0px 0px 10px 6px #d9d7d7;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.portfolio-card__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio-card__desc {
  position: relative;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  box-shadow: inset 1px 0px 20px 0px #606060;
  background: rgb(94 89 124 / 70%);
  color: var(--content-color);
  font-weight: 600;
  font-size: 22px;
  padding: 40px;
  box-sizing: border-box;
  opacity: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: 0.5s;
  z-index: 2;
}

.portfolio-card__desc p:not(:last-child) {
  margin-bottom: 15px;
}

.portfolio-card:hover .portfolio-card__desc {
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.portfolio {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  padding-top: 200px;
  padding-bottom: 50px;
  box-sizing: border-box;
}

.portfolio__title {
  margin-top: 28px;
}

.portfolio__items {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(335px, 1fr));
  grid-gap: 30px;
  gap: 30px;
  padding: 0 70px;
}

@media screen and (max-width: 1075px) {
  .portfolio__items {
    padding: 0 50px;
  }
}

@media screen and (max-width: 768px) {
  .portfolio {
    padding-top: 150px;
  }
}

@media screen and (max-width: 500px) {
  .portfolio__items {
    padding: 0 20px;
  }
}

.contacts {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  position: relative;
  background: url(/portfolio/static/media/bird.c8b7203b.svg) bottom right/ 250px no-repeat;
}

.contacts__titlebcg {
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.contacts__list {
  color: var(--contrast-color-darker);
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 50px;
  margin-top: 100px;
}

.contacts__list li {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 600;
  color: var(--contrast-color);
}

.contacts__list li:not(:last-child) {
  margin-bottom: 10px;
}

.contacts__list a {
  text-decoration: none;
  color: var(--contrast-color);
  transition: 0.7s;
}

.contacts__list a:hover {
  color: var(--contrast-color-darker);
}

.contacts__tel {
  position: relative;
  transition: 0.5s;
}

.contacts__tel span {
  opacity: 0;
  position: absolute;
  bottom: 0px;
  right: -10px;
  color: var(--contrast-color-darker);
  font-size: 20px;
}

.contacts__tel:hover span {
  opacity: 100%;
  -webkit-animation: animated 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) forwards;
          animation: animated 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) forwards;
}

@-webkit-keyframes animated {
  0% {
    opacity: 0;
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(30deg);
    transform: rotateX(30deg);
    opacity: 1;
  }

  100% {
    transform: rotateX(0);
    -webkit-transform: rotateX(0);
    opacity: 1;
  }
}

@keyframes animated {
  0% {
    opacity: 0;
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(30deg);
    transform: rotateX(30deg);
    opacity: 1;
  }

  100% {
    transform: rotateX(0);
    -webkit-transform: rotateX(0);
    opacity: 1;
  }
}

.contacts__media {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.contacts__media a {
  position: relative;
  color: var(--contrast-color);
  font-size: 26px;
  text-decoration: none;
  margin-right: 10px;
  transition: 0.5s;
  width: 40px;
  height: 40px;
  padding: 2px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.contacts__media a:hover {
  color: var(--contrast-color-darker);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media screen and (max-width: 1075px) {
  .contacts {
    height: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  .contacts__media a {
    font-size: 40px;
    margin-right: 20px;
  }
}

@media screen and (max-width: 568px) {
  .contacts__list {
    font-size: 26px;
  }
}

.svg-circle__wrapper {
  position: absolute;
  top: 53%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.svg-circle {
  stroke-dasharray: 20 375;
  stroke-dashoffset: 100;
  fill: transparent;
  stroke: transparent;
  opacity: 0.5;
  transition: stroke-width 0.5s, stroke-dashoffset 0.5s, stroke-dasharray 0.5s;
}

.svg-circle__wrapper:hover .svg-circle {
  stroke: currentColor;
  stroke-dashoffset: 0;
  stroke-dasharray: 160 20;
}

